import AppModel from '../models/AppModel';
import AppView from '../views/AppView';

export default class App {
  constructor() {
    this.state = {
      url: '',
    };
    const searchField = document.createElement('input');
    searchField.type = 'text';
    searchField.id = 'searchField';
    document.body.appendChild(searchField);

    searchField.onchange = () => {
      this.state.url = `https://www.googleapis.com/youtube/v3/search?key=AIzaSyCTWC75i70moJLzyNh3tt4jzCljZcRkU8Y&type=video&part=snippet&maxResults=15&q=${searchField.value}`;
      this.start();
    };
  }

  async start() {
    const model = new AppModel(this.state);
    const data = await model.getData();
    const stats = await model.getStats(data);
    const view = new AppView(data, stats);
    view.render();
  }
}
