/* eslint-disable class-methods-use-this */
export default class AppModel {
  constructor(state) {
    this.state = state;
  }

  async getData() {
    const { url } = this.state;
    const response = await fetch(url);
    const data = await response.json();
    return data;
  }

  static extractVideoId(data) {
    return data.items.map(clip => clip.id.videoId).join(',');
  }


  async getStats(data) {
    const videoId = AppModel.extractVideoId(data);
    console.log(videoId);
    const url = `https://www.googleapis.com/youtube/v3/videos?key=AIzaSyCTWC75i70moJLzyNh3tt4jzCljZcRkU8Y&id=${videoId}&part=snippet,statistics`;
    const response = await fetch(url);
    const stats = await response.json();
    console.log(stats);
    return stats;
  }
}
