/* eslint-disable class-methods-use-this,prefer-destructuring */

export default class AppView {
  constructor(data, stats) {
    this.data = data;
    this.stats = stats;
  }


  render() {
    if (!document.getElementById('container')) {
      const content = document.createElement('div');
      content.id = 'container';
      document.body.appendChild(content);
      const list = document.createElement('div');
      list.id = 'list';
      content.appendChild(list);
    }
    document.getElementById('list').innerHTML = this.data.items.map((item, i) => `<div class="item">
                <div class="preview" style="background-image:url('${item.snippet.thumbnails.high.url}')"></div>
                <div class="title"><a href='https://www.youtube.com/watch?v=${item.id.videoId}'>${item.snippet.title}</a></div>
                <div class="channel">${this.stats.items[i].snippet.channelTitle}</div>
                <div class="views">${this.stats.items[i].statistics.viewCount}</div>
                <div class="date">${this.stats.items[i].snippet.publishedAt.slice(0, 10)}</div>
                <div class="description">${this.stats.items[i].snippet.description}</div>
         </div>`).join('');
    const slider = document.getElementById('list');
    let isDown = false;
    let startX;
    let scrollLeft;
    let walk;

    // slider engine
    slider.addEventListener('pointerdown', (e) => {
      isDown = true;
      slider.classList.add('active');
      startX = e.pageX - slider.offsetLeft;
      scrollLeft = slider.scrollLeft;
    });
    slider.addEventListener('pointerleave', () => {
      isDown = false;
      slider.classList.remove('active');
    });
    slider.addEventListener('pointerup', () => {
      isDown = false;
      if (walk >= 50) {
        slider.scrollLeft = scrollLeft + walk;
        slider.scrollLeft = scrollLeft - document.getElementById('container').offsetWidth;
      }
      if (walk <= -50) {
        slider.scrollLeft = scrollLeft + walk;
        slider.scrollLeft = scrollLeft + document.getElementById('container').offsetWidth;
      }
      if (walk >= -50 && walk <= 50) {
        slider.scrollLeft = scrollLeft + walk;
      }
      slider.classList.remove('active');
    });
    slider.addEventListener('pointermove', (e) => {
      if (!isDown) return;
      e.preventDefault();
      const x = e.pageX - slider.offsetLeft;
      walk = x - startX; // scroll-fast
      slider.scrollLeft = scrollLeft - walk;
    });
  }
}
